<template>
  <div class="page">
    <div class="content">
      <div class="articles">
        <ul class="list">
          <router-link tag="li" class="li" :to="'/articles/'+item.id" v-for="(item,xh) in articles" :key="xh">
            <div class="title">{{item.title}}</div>
            <div class="time">{{item.mtime}}</div>
            <div class="ms">
                <div class="jl" v-html="jie(item.nr)"></div>
            </div>
            <div class="img" v-if="item.tpic"><img :src="item.tpic"></div>
            <div class="img" v-else><img src="@/assets/myimg/logo.png" style="opacity: .2"></div>
          </router-link>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import base from "../myapis";
export default {
    name: 'articles',
    data() {
        return {
            articles: []
        }
    },
    created() {
        fetch(base.base1+'article').then((rs) => {
            return rs.json()
        }).then((data) => {
            data=data.data.list
            data.sort((a, b) => {
                let atime = a.mtime.replace(/-/g, '')
                let btime = b.mtime.replace(/-/g, '')
                if (atime >= btime&&a.id>b.id) {
                    return -1
                } else {
                    return 1
                }
            })
            this.articles = data
        })


    },
    methods: {
        jie(html) {
            //let much
            let reg = new RegExp('<[^>]+>', 'gi');  //过滤所有的html标签，不包括内容
            if (typeof html != 'string') {  //不是字符串
                return html;
            }
            let last = html.replace(reg, '')
            if (window.innerWidth > 768) {
                //much = 60
            } else {
                //much = 20
            }

            //let lh = last.substr(0, much)
            return '<p>' + last + '</p>'
        }
    },
    computed: {}
}
</script>

